
import React, { useState, useEffect} from 'react';
import './CreditCar.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import visaLogo from '../../../Images/visaLogo.png';

function CreditCar ({num}) {
    const var1= num;
    const [numberCardValid, setNumberCardValid] = useState("");
    const [hasError, setErrors] = useState(false);
    const [planets, setPlanets] = useState({});

    const cc_format = (value) => {
        var v = value.toString().replace(/\s+/g, '').replace(/[^0-9]/gi, '')
        var matches = v.match(/\d{4,16}/g);
        var match = matches && matches[0] || ''
        var parts = []
        var i = 0;
        var len = 0;
        for (i, len=match.length; i<len; i+=4) {
          parts.push(match.substring(i, i+4));
        }
        if (parts.length) {
          return parts.join('-')
        } else {
          return value
        }
      }

      const validationCard = (evt) => {
       const dateCard = {
         numberCard: var1
        }
            const res =  fetch('/isLoggedIn', {
              method: 'post',
              header: {
                'Accept': 'application/json',
                'Content-type': 'application/json'
              },
              body: JSON.stringify({
                numberCard: dateCard
              })
            });
             res
              .json()
              .then(res => setPlanets(res))
              .catch(err => setErrors(err));
      
          console.log(dateCard);
      }


    

    return (
      <Container fluid="md">
        <Row >
          <Col>
        <div className='credit-card'>
            <div className='credit-card__logo'>
            <img className='logo' src={visaLogo} width="50"/>
            </div>

            <div className='credit-card__number'>
              <input  name="numberCard" 
                      onChange={(e) =>
                        setNumberCardValid( {name: e.target.value} )} 
                      id="numberCard"
                      className="btn btn-primary btn-block"
                      type="input" 
                      value={cc_format(var1)} />
            </div>
            
            <div className='credit-card__info'>
                <div className='credit-card__info_name'>
                    <div className='credit-card__info_label'>Globant S.A</div>
                    <div>Gustavo Quintana</div>
                </div>

                <div className='credit-card__info_expiry'>
                    <div className='credit-card__info_label'>Venc:</div>
                    <div>05/2025</div>
                </div>
            </div>
        </div>
        </Col>
        <Col sm="12 pt-3 ml-4">
          <button onClick={validationCard} className="btn btn-outline-primary btn-lg btn-block">Ingresar al Sistema</button>
        </Col>
        </Row>
        </Container>
    );
}

export default CreditCar;